.table-controls .btn-group{
  padding-left:10px;
}


/**
 * Hide input in table
 */
td .td-input{display:none;}
td .td-input input[type="text"]{width:100%;}

tr.show-input td .td-input{display:block;}
tr.show-input td .td-text{display:none;}


/**
 * hide table controls
 */
.table-controls{
  .btn-group{padding-left:10px;}

  .btn-group-edit{
    display:none;
    padding-left: 10px;
  }

  &.edit{
    .btn-group-edit{display:inline-block;}
    .btn-group{display:none;}
  }
}
